import {ICarrier} from "./ICarrier";

export class Carrier{
	public static USCarriers: ICarrier[] = [{"carrier":"att","domain":"txt.att.net"},{"carrier":"tmobile","domain":"tmomail.net"},{"carrier":"verizon","domain":"vtext.com"},{"carrier":"cricket","domain":"sms.mycricket.com"},{"carrier":"uscellular","domain":"email.uscc.net"},{"carrier":"virginmobile","domain":"vmobl.com"},{"carrier":"boostmobile","domain":"myboostmobile.com"},{"carrier":"metropcs","domain":"mymetropcs.com"}];
	public static GetEmailAddress(carrier: string, phoneNumber: number): string{
		if(!carrier){
			return null;
		}
		let matchCarrier: Carrier[] = Carrier.USCarriers.filter(d=>{return (d.carrier===carrier.toLowerCase())});

		if(!phoneNumber || !matchCarrier || matchCarrier.length === 0){
			return null;
		}

		return `${phoneNumber}@${matchCarrier[0].domain}`;
	}
	carrier: string;
	domain: string;

	constructor(carrier: string, domain: string){
		this.carrier = carrier.toLowerCase();
		this.domain = domain.toLowerCase();
	}
}



